<template>
    <div class="tabbar">
        <div :class="{'item-container':true,selected:selNum==1}" @click="toPage(1)"><i class="iconfont icon-shouye"></i>首页</div>
        <div :class="{'item-container':true,selected:selNum==2}" @click="toPage(2)"><i class="iconfont icon-chanpinzhongxin1"></i>产品中心</div>
        <div :class="{'item-container':true,selected:selNum==3}" @click="toPage(3)"><i class="iconfont icon-gouwuche"></i>购物车</div>
        <div :class="{'item-container':true,selected:selNum==4}" @click="toPage(4)"><i class="iconfont icon-huiyuan"></i>会员中心</div>
  </div>
</template>
<script>
  export default {
    data(){
        return {
            selNum:1
        }        
    },
    watch:{
        selNum: function(){
            this.$emit('customEvent',this.selNum);
        }
    },
    beforeMount:function(){
        this.currentPage(this.$route.path);
    },
    methods: {        
        currentPage(pathStr){
            if(pathStr == '/'){
                this.selNum = 1;
            }
            if(/pro|details/.test(pathStr)){
                this.selNum = 2;
            }
            if(/cart|cforder/.test(pathStr)){
                this.selNum = 3;
            }
            if(/member/.test(pathStr)){
                this.selNum = 4;
            }
        },

        toPage(num){
            this.selNum = num;
            let url = "";
            switch(num){
                case 1:
                    url = '/';
                    break;
                case 2:
                    url = '/pro';
                    break;
                case 3:
                    url = '/cart';
                    break;
                case 4:
                    url = '/member';
                    break;
            }
            this.$router.push(url);
        }
    }
  };
</script>
<style lang="less" scoped>
  @import '../assets/css/basic.less';
  .tabbar{
      width: 100%;
      height: 132px;
      background: #ffffff;
      border-top: 1px solid #ddd;
      display: flex;
      justify-content: space-between;
      position: fixed;
      bottom: 0;
      left: 0;
      .item-container {
        width: 25%;
        text-align: center;
        font-size:  @tbfs;
        cursor: pointer;
        .iconfont {
            display: block;
            margin: 12px 0 3px;
            font-size: @tbfs_icon;
        }        
      }
      .selected{
          color: @color_dark;
      }
  }
</style>



