<template>
  <div class="cforder">
    <div class="head"><i class="iconfont icon-jiantou2" @click="handleBack"></i><h1>商品详情</h1></div>    
    <div class="photo">
       <ul>
           <li v-for="(tem,index) in pic" :key="index"><img :src="require('../'+tem.lg)"></li>
       </ul>
       <span>1<small>/6</small></span>
    </div>   
    <div class="desc">
       <p>{{pro.sku_name}}</p>
       <p><small>￥</small>{{pro.price}}</p>
    </div>
    <div class="spec">
       <div><p>已选</p><p class="value">{{selected}}件</p></div><i class="iconfont icon-iconfontgengduo" @click="showPannel"></i>       
    </div>
    <div class="details">
       <h2>详情</h2>
       <div>
           <!-- {{pro.details}} -->
           <p><img :src="require('../'+pic[0].lg)"></p>
           <p>商品名称：{{pro.spu_name}}</p>
           <p>规格型号：{{spec}}</p>
           <p>销售单价：{{pro.price}}元</p>
       </div>
    </div>
    <div :class="{'pannel-spec':true,avtived:isShow}">
        <div class="top">
            <img v-for="(tem,index) in pic" :key="index" :src="require('../'+tem.lg)">
            <div>
                <p>商品名称：{{pro.spu_name}}</p>
                <p><small>￥</small>{{pro.price}}</p>
                <p>商品编码：{{pro.sku_no}}</p>
            </div>
            <a href="javascript:;" class="close" @click="closePannel"><i class="iconfont icon-guanbi"></i></a>              
        </div>
        <div class="content">
            <div class="speclist clear">
                <dl class="clear" id="attr0">
                    <dt>{{skuattr[0].name}}</dt>
                    <dd v-for="(a,idx) in attr0"
                        :key="idx"
                        :class="{selected:(a.av_id==vid0)}" 
                        @click="checkSku(a.attr_id+':'+a.av_id,1)"
                        :data-attr0="a.attr_id+':'+a.av_id"
                        >{{a.value}}</dd>
                </dl>
                <dl class="clear" id="attr1">
                    <dt>{{skuattr[1].name}}</dt>
                    <dd v-for="(a,idx) in attr1"
                        :key="idx"
                        :class="{selected:(a.av_id==vid1),disabled:a.flag==0}" 
                        @click="a.flag==1 && checkSku(a.attr_id+':'+a.av_id,0)"
                        :data-attr1="a.attr_id+':'+a.av_id"
                        >{{a.value}}</dd>
                </dl>
            </div>
            <div class="wraper-count">
                <p>购买数量</p>
                <div class="count">
                <span @click="modifyCount(false)">-</span><input type="text" v-model="count"><span @click="modifyCount(true)">+</span>
                </div>
            </div>       
        </div>
        <div class="foot">
            <span @click="closePannel"><i class="iconfont icon-jiantou2"></i>返回</span>
            <a href="javascript:;" @click="addCart">加入购物车</a>
        </div>
    </div>
    <div :class="{mark:true,show:isShow}">&nbsp;</div>
    <div class="btn">
       <span @click="handleBack"><i class="iconfont icon-jiantou2"></i>返回</span>
       <a href="javascript:;" class="addcart" @click="showPannel">加入购物车</a>
       <a href="javascript:;" class="buynow" @click="buyNow">立即购买</a>
    </div>
  </div>    
</template>
<script>
  export default {
    data: () => ({
      isShow:false, //规格面板状态
      sku_id:null,
      attr_id0:null, //第一属性名ID
      vid0:null, //第一属性值ID
      attr0:[], //第一属性列表
      vid1:null, //第二属性值ID
      attr1:[], //第二属性列表
      pro:null,  //商品信息
      pic:[],  //商品图片
      skuattr:null, //商品属性标题
      skulist:null, //sku和属性对应列表
      attrlist:null, //sku属性名、值对应列表
      spec:"",  //商品规格
      count:1,  //商品数量
    }),    
    created () {
        var sku_id = this.$route.params.id; 
        this.sku_id = sku_id;
        // console.log(this.urlArr);
    },
    computed:{
        selected: function(){
            return this.spec +" "+ this.count;
        },
        urlArr:function(){
            return this.$store.getters.urlArr;
        },
        uid:function(){
            return this.$store.getters.uid;
        }        
    },
    watch:{
        sku_id:function(){
            this.loadProduct(this.sku_id);
        },
        $route:function(){

        }
    },
    methods: {
      handleBack(){
          this.$router.push(this.urlArr[0]);
      },
      showPannel(){
          this.isShow = true;
      },
      closePannel(){
          this.isShow = false;
      },
      loadProduct(sku_id){
        this.attr0 = [];
        this.attr1 = [];
        axios.get(this.GLOBAL.serverSrc+'toyomall/data/product/product.php',{params:{sku_id}})
        .then(res=>{
			// console.log(res);
            var data = res.data,
            pro = data.data,
			pic = data.pic,
			attr = data.attr,
            skulist = data.skulist,
            attrlist = data.attrlist;
            
            this.pro = pro;
            this.pic = pic;
            this.skulist = skulist;
            this.attrlist = attrlist;

			// 商品属性
			// 属性
            var skuattr = [attr[0]],
                attr_id0 = attr[0].attr_id;
            this.attr_id0 = attr_id0;
            
			for(var i=1;i<attr.length;i++){
				if(attr[i].attr_id!=attr_id0){
                    skuattr.push(attr[i]);
                    break;
                }
            }
            
            this.skuattr = skuattr;
			
			// console.log(skuattr);

			var sku_attr = pro.sku_attr.replace(/\[|\]/g,""),
				color_id = parseInt(sku_attr.slice(sku_attr.indexOf(":")+1,sku_attr.indexOf(","))), // 当前属性值1 ID
                version_id = parseInt(sku_attr.slice(sku_attr.lastIndexOf(":")+1)); // 当前属性值2 ID
            
            this.vid0 = color_id;
            this.vid1 = version_id;
            
            // 根据属性ID 获取value 得到规格
            this.spec = "";
            for(var item of attr){
                if([color_id,version_id].indexOf(parseInt(item.av_id))!=-1){
                    this.spec += item.value+" ";
                }
            }
			
			var totalVersion = []; // 所有版本
			for(var v of attr){
				if(v.attr_id==skuattr[0].attr_id)
					totalVersion.push(parseInt(v.av_id));
			}
			// console.log("totalVersion: "+totalVersion);
			
			var _arrColor=[],
				_arrVersion=[];
			for(var item of skulist){
				var itemattr = item.sku_attr.replace(/\[|\]/g,"");
				var cid = itemattr.slice(itemattr.indexOf(":")+1,itemattr.indexOf(","));
				var vid = itemattr.slice(itemattr.lastIndexOf(":")+1);
				_arrColor.push(cid);
				_arrVersion.push(vid);
			}
			// console.log(_arrColor);
			// console.log(_arrVersion);

			var arrCid=[], // 所有颜色 ID
				arrVid=[];	// 颜色对应的版本 ID

			for(var i=0,_arr=[];i<_arrColor.length;i++){			
				if(i==0){
					arrCid.push(parseInt(_arrColor[i]));
					_arr.push(parseInt(_arrVersion[i]));
					continue;
				}else{
					if(arrCid.indexOf(parseInt(_arrColor[i]))==-1){
						arrCid.push(parseInt(_arrColor[i]));
					}
					if(_arrColor[i]==_arrColor[i-1]){
						_arr.push(parseInt(_arrVersion[i]));
						if(i==_arrColor.length-1){
							arrVid.push(_arr);
						}
						continue;
					}else{
						arrVid.push(_arr);
						_arr=[];
						_arr.push(parseInt(_arrVersion[i]));
					}								
				}				
			}
			// console.log(arrCid);
			// console.log(arrVid);

			// 选中颜色下，对应的可用版本
            var usableVersion = arrVid[arrCid.indexOf(color_id)];
            // console.log(usableVersion);

            // 判断当前颜色是否存在所有版本,如不存在，flag标记为0，存在则标记为1
            for(var item of attr){
                if(item.attr_id==attr_id0){
                    this.attr0.push(item);
                }else{                        
                    if(usableVersion.indexOf(parseInt(item.av_id))!=-1){
                        item['flag'] = 1;
                    }else{
                        item['flag'] = 0;
                    }
                    this.attr1.push(item);
                }
            }
        })
      },
      checkSku(val,flag){
            var colorAV = "",
                versionAV = "";
            if(flag){
                colorAV = val;
                versionAV = document.querySelector('#attr1>.selected').dataset.attr1;
            }else{
                colorAV = document.querySelector('#attr0>.selected').dataset.attr0;
                versionAV = val;
            }
				
            var skuAttr = "["+colorAV+","+versionAV+"]";
            var av_id = parseInt(colorAV.slice(colorAV.indexOf(":")+1));
            
            var skuId;
			for(var item of this.skulist){
				if(item.sku_attr==skuAttr){
					skuId = item.sku_id;
				}
			}

			if(!skuId){
				for(var attr of this.attrlist){
					if(av_id==attr.av_id){
						skuId = attr.sku_id;
						break;
					}
				}
            }
            this.sku_id = skuId;			
      },
      modifyCount(isAdd){
          if(isAdd){
              this.count++;
          }else{
              if(this.count==1){
                  return;
              }
              this.count--;
          }
      },
      addCart(){
          var uid = this.uid,
              sku_id = this.sku_id,
              count = this.count;

          if(uid){
            axios({
                method:'get',
                url:this.GLOBAL.serverSrc+'toyomall/data/cart/addcart.php',
                params:{uid,sku_id,count}
            })
            .then(res=>{
                if(res.data.code==200){
                    confirm('添加成功！')
                    this.isShow = false;
                }              
            })
          }else{
              this.$router.push({
                  path:'/login',
                  query:{ redirect: this.$route.path }
              });
          }
      },
      buyNow(){
          var uid = this.uid,
              sku_id = this.sku_id,
              count = this.count,
              is_checked = 1;
              
          if(uid){
              axios.get(this.GLOBAL.serverSrc+'toyomall/data/cart/addcart.php',{params:{uid,sku_id,count,is_checked}})
                .then(()=>{
                    this.$router.push('/cforder/'+sku_id);
                })
          }else{
              this.$router.push('/cforder/'+sku_id);
          }       
      }
    }
  }
</script>
<style lang="less" scoped>
  @import '../assets/css/basic.less';
.cforder{
   background: #f9f9f9;
   height: 100%;
  .head{
    width: 100%;
    height: 120px;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    background: #FFF;
    border-bottom: 1px solid #ddd;
    line-height: 120px;
    h1{
        margin: 0 auto;
    }
    i{
      font-size: @lg;
      position: absolute;
      left: 5%;
      cursor: pointer;
    }
  }
  .photo{
    margin-top: 121px;
    position: relative;
    background: #fff;
    ul{
        li{
            img{width:100%;}
        }
    }
    span{
        font-size: @lg;
        display: block;
        padding: 0.2em 0.8em;
        background: #ddd;
        border-radius:1em;
        color: #fff;
        position: absolute;
        right: 1em;
        bottom: 1em;
    }
  }
  .desc{
      line-height: 2em;
      background: #fff;
      padding: 0 5% 1em;
      font-size: @md;
      p:last-child{
          color: @color_normal;
      }
  }
  .spec{
      padding: 0.8em 5%;
      margin: 0.5em 0;
      font-size: @md;
      display: flex;
      justify-content: space-between;
      background: #fff;
      div:first-child{
          p{
              display: inline-block;
              line-height: @lg*1.5;
          }
          .value{
              font-size: @md;
              font-weight: 600;
              margin-left: 1em;
          }
      }
      i{
          font-size: @lg*1.5;
          cursor: pointer;
      }
  }
  .details{
      margin-bottom: 120px;
      background: #fff;
      padding: 0.5em 5% 2em;
      h2{
          width: 12%;
          padding: 0.3em 0;
          border-bottom: 3px solid @color_normal;
          text-align: center;
      }
      div{
          font-size: @md;
          line-height: 2em;
          p{
            img{
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                width: 80%;
            }
          }
      } 
  }  
  .pannel-spec{
    width: 90%;
    height: 75%;
    padding: 0 5%;
    position: fixed;
    left:0;
    bottom: 0;
    z-index: 99;
    background: #fff;
    font-size: @md;
    overflow: auto;
    transition: all .5 linear;
    display: none;    
    .top{
        width: 100%;
        background: #fff;
        height: 200px;
        padding-top: 0.6em;
        position: fixed;
        top: 25%;
        left: 0;
        img{
            float: left;
            margin-right: 2em;
            vertical-align: bottom;
            height: 100%;
        }
        div{
            position: absolute;
            left: 260px;
            bottom: 0.8em;
            line-height: 1.8em;
            p:nth-child(2){
                color:@color_normal;
            }
            p:nth-child(3){
                font-size: @md*0.9;
            }
        }
        .close{
            position: absolute;
            top: 0.5em;
            right: 1.5em;
            display: block;
            padding: 0.2em;
            text-align: center;
            i{
                font-size: @md;
            }
        }
    }
    .top:after{
        content: '';
        display: block;
        clear: both;
    }
    .content{
        margin: 230px 0 170px;
        .speclist{
            dl{
                margin-top: 0.8em;
                line-height: 2em;
                dt{
                    margin-bottom: 0.5em;
                }
                dd{ 
                    float: left;
                    background: #f5f5f5;
                    padding: 0 1em;
                    margin-right: 0.5em;
                    border: 1px solid transparent;
                    cursor: pointer;
                }
                dd.selected{
                    color: @color_normal;
                    border-color: @color_normal; 
                }
                dd.disabled{
                    color: #ddd;
                    cursor: default;
                }
            }
        }
        .wraper-count{
            display: flex;
            justify-content: space-between;
            margin-top: 1.2em;
            font-size: @md;
            .count{
                input{
                    margin: 0;
                    padding: 0;
                    width: 60px;
                    text-align: center;
                    border: 1px solid #eee;
                    outline: none;
                    background: #FFF;
                    line-height: 38px;
                    position: relative;               
                    top: 2px;
                    font-size: @md;
                }
                span{
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    color: #FFF;
                    background: @color_normal;
                    text-align: center;
                    cursor: pointer;
                }
            }
        }
    }
    .foot{
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 120px;
        line-height: 120px;
        background: #f0f0f0;
        display: flex;
        justify-content: space-between;
        span{
            display: block;
            width: 30%;
            text-align: center;
            cursor: pointer;
            i{
                font-size: @lg;
                margin-right: 0.5em;
            }
        }
        a{
        display: block;
        width: 30%;
        line-height: 120px;
        background: @color_normal;
        color: #fff;
        text-align: center;
        font-size: @md;
        }
    }
  }
  .pannel-spec.avtived{
      display: block;
  }
  .mark{
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 89;
      background: #000;
      opacity: .5;
      display: none;
  }
  .mark.show{
      display: block;
  }
  .btn{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 120px;
    line-height: 120px;
    background: #f0f0f0;
    display: flex;
    justify-content: space-between;
    font-size: @md;
    span{
        display: block;
        width: 30%;
        text-align: center;
        margin-right: 10%;
        cursor: pointer;
        i{
            font-size: @lg;
            margin-right: 0.5em;
        }
    }
    a{
      display: block;
      text-align: center;
      font-size: @md;
      width: 30%;
      color: #fff;
      line-height: 120px;
    }
    a.buynow{
      background: @color_normal;
    }
    a.addcart{
      background: @color_light;
    }
  }
}
</style>



